import { BigNumber } from 'ethers';
import React, { useEffect, useState } from 'react';
import { Row, Col } from 'react-bootstrap';
import useBlockchain from '../../hooks/useBlockchain';
import useLocalStorage from '../../hooks/useLocalStorage';

const Profile = () => {
    const { getAccount } = useLocalStorage();
    const [contract, , , active] = useBlockchain();
    const [classicLevel, setclassicLevel] = useState(0);
    const [premiumLevel, setpremiumLevel] = useState(0);
    const [walletAddress, setwalletAddress] = useState();
    const [accountID, setaccountID] = useState();
    const [referralID, setreferralID] = useState();
    const [importedReferral, setImportedReferral] = useState('');
    

    useEffect(() => {
        if (!active) return;
        const accountID = getAccount();
        const fn = async () => {
            const user = await contract.getUser(BigNumber.from(accountID));
            setwalletAddress(user.addr)
            setclassicLevel(parseInt(user.classicLevel));
            setpremiumLevel(parseInt(user.premiumLevel));
            setaccountID(accountID)
            setreferralID(parseInt(user.referralID))
            const importedReferral = await contract.getImpClassicLevel(accountID)
            setImportedReferral(parseInt(importedReferral)) 
        };
        fn();
    }, [contract, getAccount, active]);

    return (
        <>
            <Row>
                <Col md={8} offset={3}>
                    <div className="card">
                        <div className="card-header">
                            <h5>My Profile</h5>

                            <div className="float-right">{walletAddress}</div>
                        </div>
                        <div className="card-body table-border-style">
                            <p>
                                Classic Level <br />
                                <strong>{importedReferral > classicLevel ? <>{importedReferral}</> : <>{classicLevel}</>}</strong>
                            </p>
                            <p>
                                Premium Level <br />
                                <strong>{premiumLevel}</strong>
                            </p>
                            <p>
                                Account ID <br />
                                <strong>{accountID}</strong>
                            </p>
                            <p>
                                Upline ID <br />
                                <strong>{referralID}</strong>
                            </p>
                        </div>
                    </div>
                </Col>
            </Row>
        </>
    );
};

export default Profile;
